@import "@/styles/main.scss";










































































.all-needs-container{
    width: 100%;
    padding: 110px 30px 90px;
    justify-content: space-between;
    background-color: #ffffff;

    .all-needs-heading-div{
        justify-content: center;
        align-items: center;
        margin-bottom: 50px;

        h3{
            color: #000000;
            font-size: 40px;
            font-weight: 700;
            cursor: default;
            font-weight: bold;

            .website-pinked{
                margin-left: 4px;
                margin-right: 4px;
                font-size: 40px;
                font-weight: bold;
                color: #ff5d73 !important;
            }
        }

        span{
            color: #808080;
            margin-top: 0px;
            font-size: 24px;
            font-weight: normal;
            cursor: default;

        }
    }

}

@media (max-width: 835px){
    .needs-card-container{
        grid-template-columns: 1fr !important;
    }

    .all-needs-container{
        // margin-top: 520px;

    }
}
@media (max-width: 651px){
    .needs-card-container{
        grid-template-columns: 1fr !important;
    }

    .all-needs-container{
        // margin-top: 900px;
        padding: 80px 21px 90px !important;

        h3{
          font-size: 30px !important;
        }

        span.website-pinked{
          font-size: 30px !important;

        }

        img.phone{
          margin-bottom: 30px;
        }
    }
}
