@import "@/styles/main.scss";












































































































































.needs-card-container{
    width: 100%;
    display: grid;
    grid-template-columns: 35% 30% 35%;

    .needs-card-holder{
                width: 100%;
                padding: 20px 15px;
                align-self: start;

                .left-needs-card-header{
                    display: flex;
                    justify-content: flex-end;

                     .needs-card-heading{
                        width: 75%;
                        display: flex;
                        flex-direction: column;
                        justify-content: center;
                        align-items: stretch;
                        padding-right: 15px;
                        height: 65px;
                        h4{
                            font-size: 26px;
                            font-weight: 500;
                            margin-top: 10px;
                            color: #ff5d73;
                            direction: rtr;
                            text-align: right;
                            margin-left: 0 !important;
                            cursor: default;
                            margin-bottom: 0;

                        }

                        span{
                            margin-top: 8px;
                            direction: rtr;
                            text-align: right;
                            font-size: 16px;
                            font-weight: 500;
                            word-spacing: 0.2rem;
                            letter-spacing: 0.05rem;
                            color: #000000;
                            cursor: default;
                        }
                    }

                    .needs-card-image{
                        display: flex;
                        align-self: center;
                        -webkit-box-shadow: 0px 0px 3px 0px rgba(255,93,115,1);
                        -moz-box-shadow: 0px 0px 3px 0px rgba(255,93,115,1);
                        box-shadow: 0px 0px 3px 0px rgba(255,93,115,1);
                        border: 1px solid #FF5D73;
                        padding: 10px;
                        border-radius: 4px;
                        transition: all 0s ease;

                        svg{
                            color: #FF5D73;
                            font-size: 3rem;
                            transition: all 0s ease;
                        }

                    }
                    &:hover{
                        .needs-card-image{
                            transform: rotateZ(360deg);
                            background-color: #FF5D73;
                            transition: 0.5s ease;
                            svg{
                                color: #ffffff;
                            }
                        }
                    }
                }

                .right-needs-card-header{
                    display: flex;

                     .needs-card-heading{
                        width: 75%;
                        display: flex;
                        flex-direction: column;
                        justify-content: center;
                        align-items: flex-start;
                        padding-left: 15px;
                        height: 65px;
                        h4{
                            font-size: 26px;
                            font-weight: 500;
                            color: #ff5d73;
                            margin-top: 10px;
                            direction: ltr;
                            text-align: left;
                            margin-left: 0 !important;
                            cursor: default;
                            margin-bottom: 0;

                        }

                        span{
                             margin-top: 8px;
                            direction: ltr;
                            text-align: left;
                            font-size: 16px;
                            font-weight: 500;
                            word-spacing: 0.2rem;
                            letter-spacing: 0.05rem;
                            color: #000000;
                            cursor: default;
                        }
                    }

                    .needs-card-image{
                        display: flex;
                        align-self: center;
                        -webkit-box-shadow: 0px 0px 3px 0px rgba(255,93,115,1);
                        -moz-box-shadow: 0px 0px 3px 0px rgba(255,93,115,1);
                        box-shadow: 0px 0px 3px 0px rgba(255,93,115,1);
                        border: 1px solid #FF5D73;
                        padding: 10px;
                        border-radius: 4px;
                        transition: all 0s ease;

                        svg{
                            color: #FF5D73;
                            font-size: 3rem;
                            transition: all 0s ease;

                        }

                    }
                    &:hover{

                        .needs-card-image{
                            transform: rotateZ(360deg);
                            background-color: #FF5D73;
                            transition: 0.5s ease;
                            svg{
                                color: #ffffff;
                            }
                        }
                    }
                }

                .left-needs-card-content{
                    width: 100%;
                    display: flex;
                    justify-content: flex-end;
                    margin-top: 30px;
                    align-items: center;
                    overflow: hidden;

                    p{
                        width: 100%;
                        display: flex;
                        direction: ltr;
                        text-align: right;
                        color: #808080 !important;
                        line-height: 1.9rem;
                        cursor: default;
                    }
                }

                .right-needs-card-content{
                    width: 100%;
                    display: flex;
                    justify-content: flex-start;
                    margin-top: 30px;
                    align-items: center;
                    overflow: hidden;

                    p{
                        width: 100%;
                        display: flex;
                        direction: ltr;
                        text-align: left;
                        color: #808080 !important;
                        line-height: 1.9rem;
                        cursor: default;
                    }
                }
    }
}

.phone{
    height: 480px ;
    // background-image: url(../../../../public/images/phone-lg.svg);
}

@media (max-width:1100px){

    .needs-card-container{

    grid-template-columns: 35% 30% 35%;
    }
}
