@import "@/styles/main.scss";











































































    .container-fluid{
      background-color: #ffffff;
      padding-top: 40px;

    }
    .top-heading{
        margin-top: 0px;
        padding-top: 48px;
        font-family: 'Helvetica';
        font-size: 2.5rem;
        font-weight: 700;
        color: #000000;
        cursor: default;

    }

    .how-it-works-pinked{
        margin-top: 0px;
        padding-top: 48px;

        color: #ff5d73;
        font-size: 2.5rem;
        font-weight: 700;
        margin-left: 14px;
        cursor: default;

    }

    .card-container{
        width: 24%;
        transition: all 1s ease;

    }

@media (max-width: 1020px){

    .card-container{
        width: 40% !important;
        margin-bottom:40px
    }

    .all-cards-container-div{
        flex-wrap: wrap;
    }
}

@media (max-width: 904px){

    .card-container{
        width: 45% !important;
        margin-bottom:40px
    }

    .all-cards-container-div{
        flex-wrap: wrap;
    }
}

@media (max-width: 815px){

    .card-container{
        width: 47% !important;
        margin-bottom:40px
    }

    .all-cards-container-div{
        flex-wrap: wrap;
    }
}

@media (max-width: 783px){

    .card-container{
        width: 49% !important;
        margin-bottom:40px
    }

    .all-cards-container-div{
        flex-wrap: wrap;
    }
}

@media (max-width: 650px){
    .card-container{
        width: 100% !important;
        margin-bottom:40px;
    }

    .all-cards-container-div{
      padding: 0 21px !important;
    }

    .top-heading{
      font-size: 2.1rem !important;
    }

    .how-it-works-pinked{
      font-size: 2.1rem !important;

    }
}
@media (max-width: 500px){
    .card-container{
        width: 100% !important;
        margin-bottom:40px;
    }

    .all-cards-container-div{
      padding: 0 21px !important;
    }

    .top-heading{
      font-size: 1.7rem !important;
    }

    .how-it-works-pinked{
      font-size: 1.7rem !important;

    }
}
